.heading {
  line-height: 1;

  &--primary {
    font-size: 2rem;
    color: var(--dark-grayish-cyan);
    letter-spacing: 10px;
  }

  &--secondary {
    font-size: 1.6rem;
    color: var(--grayish-cyan);
  }

  &--error {
    font-size: 1.6rem;
    color: var(--red-error);
  }
}
